    iconSize() {
        return this.width() / 2
    }

    iconPosition() {
        const x = ( this.width() - this.iconSize() ) / 2
        const y = x
        return [
            x,
            y,
        ]
    }

    render() {
        const iconSize = this.iconSize()
        const [ x, y ] = this.iconPosition()

        return html`
            <div
                class="progress-circle"
                style="--percent: ${this.percent}; --width: ${this.widthPx()}; --circ: ${this.circumferencePx()}; --border-width: ${this.borderWidth}"
                data-border
            >
                <svg class="svg-wrapper">
                    <circle
                        class="border"
                        cx="${this.center()}"
                        cy="${this.center()}"
                        r="${this.radius}"
                    >
                    </circle>
                    <circle
                        cx="${this.center()}"
                        cy="${this.center()}"
                        r="${this.radius}"
                    >
                    </circle>
                    <circle
                        class="bar"
                        cx="${this.center()}"
                        cy="${this.center()}"
                        r="${this.radius}"
                        transform="${this.rotate(-90)}"
                    >
                    </circle>
                    <svg
                        class="icon"
                        xmlns="http://www.w3.org/2000/svg"
                        width=${iconSize}
                        height=${iconSize}
                        x=${x}
                        y=${y}
                        viewBox="0 0 40 40"
                    >
                        ${this.getIconSvg()}
                    </svg>
                </svg>
            </div>
        `;
    }

    createRenderRoot() {
        return this;
    }
